const loggerState = (hookName, oldState, newState) => {
    console.group(`Hook ${hookName}`);
    console.info('Old state:', oldState);
    console.info('New state:', newState);
    console.groupCollapsed('Trace');
    console.trace('Trace');
    console.groupEnd();
    console.groupEnd();
};
const loggerMessage = (hookName, message, state) => {
    console.group(`Hook ${hookName}`);
    console.info(message);
    if (state)
        console.info('State:', state);
    console.groupCollapsed('Trace');
    console.trace('Trace');
    console.groupEnd();
    console.groupEnd();
};
const loggerData = (hookName, logs) => {
    console.group(`Hook ${hookName}`);
    logs.forEach(({ message, data }) => {
        console.info(message, data || '');
    });
    console.groupCollapsed('Trace');
    console.trace('Trace');
    console.groupEnd();
    console.groupEnd();
};

const arrToStr = (arr, splitter = ', ') => {
    if (!arr)
        return '';
    if (typeof arr === 'string')
        return arr;
    return arr.join(splitter);
};
const unflattenItem = (keys, value) => {
    const [firstKey] = keys;
    if (!firstKey)
        return {};
    if (keys.length === 1)
        return { [firstKey]: value };
    return { [firstKey]: unflattenItem(keys.slice(1), value) };
};
const unflatten = (obj, separator = '.') => {
    return Object.entries(obj).reduce((prev, [key, value]) => {
        return Object.assign(Object.assign({}, prev), unflattenItem(key.split(separator), value));
    }, {});
};
const onlyPublic = (obj) => {
    const updateObj = Object.assign({}, obj);
    Object.keys(updateObj).forEach((key) => {
        if (typeof key === 'string' && key[0] === '_')
            delete updateObj[key];
    });
    return updateObj;
};
const isObject = (obj) => {
    return (typeof obj === 'object' &&
        obj !== null &&
        Object.keys(obj).every((key) => typeof key === 'string' || typeof key === 'number' || typeof key === 'symbol'));
};
const cleanObjKeys = (reference, dirty) => {
    const result = {};
    if (Array.isArray(reference))
        return dirty;
    if (isObject(reference) && dirty && isObject(dirty)) {
        // console.log('reference', reference);
        Object.keys(reference).forEach((key) => {
            // @ts-ignore
            if (!(key in dirty))
                return;
            const referenceValue = reference[key];
            result[key] = isObject(referenceValue)
                ? cleanObjKeys(referenceValue, dirty[key])
                : dirty[key];
        });
    }
    return result;
};
